import './../Pages/AddJob/AddJob.css'
import RadioButtons from '../RadioButtons/RadioButtons'

function InputOne(props) {
    
    function handleValue(e) {
        const target = e.target;
        const { name, value } = target;
        props.setInputValues1({ ...props.InputValues1, [name]: value });
    }
    // ************** Not Working **************
    function incExperince(e) {
        e.preventDefault()
        props.InputValues1.experince = props.InputValues1.experince + 1
    }
    // ************** Not Working **************
    function decExperince(e) {
        e.preventDefault()
        if (props.InputValues1 ) {
            if( props.InputValues1.experince > 0){
                props.InputValues1.experince = eval(props.InputValues1.experince - 1)
            }
            else{
                props.InputValues1.experince = 1
            }
        }
    }
    const buttons = [
        { name: 'Male' , color: 'black' },
        { name: 'Female' , color: 'black' },
        { name: 'No Profrence', color: 'black' },
    ]

    return (
        <div>
            <div className="Ah-container">
                <div className="Ah-container-white">
                    <div className="Ah-title">
                        <h1>Business Information</h1>
                    </div>
                    <form action="">
                        <div className="row">
                            <div className="col-lg-6 col-sm-12 d-flex flex-column">
                                <label className='mb-1 star'>Job Title</label>
                                <input
                                    id='jobtitle'
                                    type="text"
                                    value={props.InputValues1.jobTitle}
                                    placeholder='Example: Art Director'
                                    name='jobTitle'
                                    onChange={handleValue}
                                />
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="jobrole" className="star mb-2">Job Role </label>
                                <select name="jobrole" value={props.InputValues1.jobRole } onChange={handleValue} id="jobrole">
                                    <option selected disabled value="1">Select</option>
                                    <option  value="API">From API</option>

                                </select>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="joblevel" className="star mb-2">Job Level </label>
                                <select name="joblevel" id="joblevel" value={props.InputValues1.jobLevel } onChange={handleValue}>
                                    <option selected disabled value="">Select</option>
                                    <option value="Junior">Junior</option>
                                    <option value="Mid Level">Mid Level</option>
                                    <option value="Senior">Senior</option>
                                    <option value="Expert">Expert</option>

                                </select>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="Experince" className="star mb-2">Job Experince </label>
                                <div className='position-relative'>
                                    <button className='op-plus' onClick={incExperince}>+</button>
                                    <input
                                        id='Experince'
                                        type="text"
                                        name='Experince'
                                        value={props.InputValues1.experince }
                                        placeholder='Experince'
                                        onChange={handleValue}
                                    />
                                    <button className='op-minus' onClick={decExperince}>-</button>
                                </div>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="educationlevel" className="star mb-2">Education Level </label>
                                <select name="educationlevel" id="educationlevel" value={props.InputValues1.educationLevel } onChange={handleValue}>
                                    <option selected disabled value="">Select</option>
                                    <option  value="baccalaureate">Baccalaureate(School)</option>
                                    <option  value="institute">Institute</option>
                                    <option  value="bachelor">Bachelor</option>
                                    <option  value="master">Master</option>
                                    <option  value="doctorate">Doctorate</option>

                                </select>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="Languages" className="star mb-2">Languages </label>
                                <select name="Languages" id="Languages" value={props.InputValues1.languages } onChange={handleValue}>
                                    <option selected disabled value="">Select</option>
                                    <option value="En">English</option>
                                    <option value="Ar">Arabic</option>

                                </select>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label for="agerange" className="star mb-2">Age Range </label>
                                <select name="agerange" id="agerange" value={props.InputValues1.ageRange } onChange={handleValue}>
                                    <option selected disabled value="">Select</option>
                                    <option value="15to20">15-20</option>
                                    <option value="20to25">20-25</option>
                                    <option value="25to30">25-30</option>
                                    <option value="30to35">30-35</option>
                                    <option value="35to40">35-40</option>
                                    <option value="40to45">40-45</option>
                                    <option value="+45">+45</option>
                                </select>
                            </div>
                            <div className=' col-lg-6 col-sm-12 d-flex flex-column'>
                                <label className='mb-1 star'>Gender</label>
                                <div >
                                    <RadioButtons buttons={buttons} />
                                </div>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default InputOne